
body {
    margin: 0;
    overflow: hidden;
    font-family: 'Poppins', sans-serif;
    /*background-color:black;*/
}
pre {
    font-size: 7em;
}
.tick line {
    stroke: #C0C0BB;
}
.tick text {
    fill: #635F5D;
    cursor: default;
}
.axis-label {
    font-size: 2.5em;
    fill: #635F5D;
}
